body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font: 12pt "Tahoma";
  border: 1px solid black;
}
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}
.page {
  width: 210mm;
  min-height: 297mm;
  padding: 10mm;
  margin: 0mm auto;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}
.subpage {
  padding: 0;
  height: 270mm;
  text-align: center;
}
table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  text-align: center;
}
table td {
  width: 25%;
  padding: 5px;
}
.page_header {
  width: 100%;
  border-bottom: 1px solid black;
  margin-bottom: 15px;
}
ul {
  margin: 0;
  padding-left: 30px;
  text-align: left;
  font-size: large;
}
.break {
  width: 100%;
  height: 2px;
  border-bottom: 2px solid black;
}
.main_p {
  margin: 0;
  text-align: left;
  font-size: large;
  text-decoration: underline;
  font-size: large;
  font-weight: bold;
  margin-bottom: 10px;
}
.sub_p {
  margin: 0;
  text-align: left;
  font-size: large;
  font-style: italic;
  font-size: large;
  font-weight: bold;
}
.sub_title {
  margin-top: 15px;
  margin-bottom: 15px;
  text-align: center;
  font-size: large;
  font-style: italic;
  font-size: large;
  font-weight: bold;
}
.Print_Button {
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 15px;
  height: fit-content;
  width: fit-content;
  text-align: center;
  background-color: rgba(0, 90, 124, 1);
  color: white;
  cursor: pointer;
  border-radius: 15px;
  font-weight: bold;
}
@page {
  size: A4;
  margin: 0;
}
@media print {
  html,
  body {
    width: 210mm;
    height: 297mm;
  }
  .page {
    margin: 0;
    border: initial;
    border-radius: initial;
    width: initial;
    min-height: initial;
    box-shadow: initial;
    background: initial;
    page-break-after: avoid;
    page-break-before: avoid;
  }
  .subpage {
    margin: 0;
    border: initial;
    border-radius: initial;
    width: initial;
    min-height: initial;
    box-shadow: initial;
    background: initial;
    page-break-after: avoid;
    page-break-before: avoid;
  }
  .Header {
    display: none;
  }
  .Print_Button {
    display: none;
  }
  /*  */
  table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    text-align: center;
  }
  table td {
    width: 25%;
    padding: 5px;
  }
  .page_header {
    width: 100%;
    border-bottom: 1px solid black;
    margin-bottom: 15px;
  }
  ul {
    margin: 0;
    padding-left: 30px;
    text-align: left;
    font-size: large;
  }
  .break {
    width: 100%;
    height: 2px;
    border-bottom: 2px solid black;
  }
  .main_p {
    margin: 0;
    text-align: left;
    font-size: large;
    text-decoration: underline;
    font-size: large;
    font-weight: bold;
    margin-bottom: 10px;
  }
  .sub_p {
    margin: 0;
    text-align: left;
    font-size: large;
    font-style: italic;
    font-size: large;
    font-weight: bold;
  }
  .sub_title {
    margin-top: 15px;
    margin-bottom: 15px;
    text-align: center;
    font-size: large;
    font-style: italic;
    font-size: large;
    font-weight: bold;
  }
}
